import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-grammar-evaluate',
  templateUrl: './grammar-evaluate.component.html',
  styleUrls: ['./grammar-evaluate.component.css']
})
export class GrammarEvaluateComponent implements OnInit {
  public grammarData:any;
  public isResult:boolean=false;
  public answers:any=[];
  public isShowResult:boolean=false;
  
  constructor(private _activeRoute:ActivatedRoute) { }

  ngOnInit() {
    this.grammarData = this._activeRoute.snapshot.data['evaluateData'];

  }

  generateAnswerFormat() {
    this.answers = new Array<any>(this.grammarData.questions.length - 1);
    this.answers.fill('');
  }

  onEvaluate(){
    this.isResult=true;
  }

  getDataQ1(index, value) {
    console.log(index, value)
    this.answers[index] = value;
  }

}
