import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VocabEvaluateComponent } from './vocab-evaluate/vocab-evaluate.component';
import { RouterModule, Routes } from '@angular/router';
import { VocabDetailResolver } from './vocab-evaluate.reslover';
import { BasicModule } from '@app/shared/basic-components/basic.module';
import { GrammarEvaluateComponent } from './grammar-evaluate/grammar-evaluate.component';
import { GrammarEvaluateResolver } from './grammar-evaluate.reslover';

const routes: Routes = [

  {
    path: 'vocabulary/:qcode/:questionId',
    component: VocabEvaluateComponent,
    resolve: {
      evaluateData: VocabDetailResolver
    }
  },
  {
    path: 'grammar/:questionId',
    component: GrammarEvaluateComponent,
    resolve: {
      evaluateData: GrammarEvaluateResolver
    }
  }
]

@NgModule({
  declarations: [
    VocabEvaluateComponent,
    GrammarEvaluateComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicModule,
  ],
  providers:[
    VocabDetailResolver,
    GrammarEvaluateResolver
  ]
})
export class TrainingEvaluateModule { }
